import { useContext } from 'react';
import NumberFormat from 'react-number-format';
import { ScrapeContext } from './scrapeContext';

const Data = () => {
  const { scrapes } = useContext(ScrapeContext)
  return (<div>
    <h2>Top 100 Profiles sorted by number of Followers</h2>
    <table>
      <thead>
        <tr>
          <th>Index</th>
          <th>InstagramId</th>
          <th>Name</th>
          <th>Number of followers</th>
          <th>Dreister Follower?</th>
        </tr>
      </thead>
      <tbody>
        {scrapes.map((scrape,index) => (
          <tr key={scrape.instagramId}>
            <td>{index +1}</td>
            <td>{scrape.instagramId}</td>
            <td>{scrape.name}</td>
            <td style={{textAlign:'right'}}><NumberFormat value={scrape.nbFollowers} displayType={'text'} thousandSeparator={' '}/></td>
            <td>{scrape.isFollowingDreister ? 'YES' : 'NO'}</td>
          </tr>
        ))}
      </tbody>
    </table>
  </div>)
}

export default Data;
